<template lang="pug">
  v-dialog(v-model='dialog' persistent max-width='600px')
    template(v-slot:activator='{ on }')
      v-btn(class="mr-4" outlined color='grey darken-2' v-on='on') Add Event
    v-card
      v-card-title
        span(class="headline") Add a New Event
      v-card-text
        v-container
          v-row
            v-form(lazy-validation ref="form" style="width:100%;")
              v-col(cols="12")
                v-select(:items="employees" v-model="eventEmployee" label="Employee*" :rules="rules" filled required)
                v-text-field(label="Event Title*" v-model="eventTitle" hint="this is what appears in calendar before clicking more" persistent-hint :rules="rules" filled required)
                v-row
                  DateField(:placeholder="firstPlace" @dates="getStartDate")
                  TimePicker(:label="firstTime" :rules="rules" @time="getStartTime")
                  DateField(:placeholder="lastPlace" @dates="getEndDate")
                  TimePicker(:label="lastTime" :rules="rules" @time="getEndTime")
                v-textarea(label="Event Details" v-model="eventDetails" rows="1" filled auto-grow)
              v-card-actions
                <small>*indicates required field</small>
                v-spacer
                v-btn(color='blue darken-1' text @click='cancel') Close
                v-btn(color='blue darken-1' text @click='addEvent') Save
</template>

<script>
import db from "@/components/firebaseInit";
import DateField from "@/components/DateField";
import TimePicker from "@/components/TimePicker";

export default {
  name: "AddCalEvent",
  components: {
    DateField,
    TimePicker
  },
  props: ["employees"],
  data() {
    return {
      dialog: false,
      valid: false,
      items: this.employees,
      eventEmployee: null,
      eventTitle: null,
      eventStart: null,
      eventEnd: null,
      eventStartTime: null,
      eventEndTime: null,
      firstPlace: "Date Started*",
      lastPlace: "Date Finished*",
      firstTime: "Time Started",
      lastTime: "Time Finished",
      eventDetails: null,
      rules: [v => !!v || "This is required"]
    };
  },
  computed: {
    completeTitle: function() {
      return `${this.eventEmployee} ${this.eventTitle}`;
    },
    completeStart: function() {
      return `${this.eventStart} ${this.eventStartTime}`;
    },
    completeEnd: function() {
      return `${this.eventEnd} ${this.eventEndTime}`;
    }
  },
  methods: {
    cancel() {
      this.$refs.form.reset();
      this.dialog = false;
    },
    addEvent() {
      this.$refs.form.validate();
      if (
        !this.eventEmployee ||
        !this.eventTitle ||
        !this.eventStart ||
        !this.eventEnd ||
        !this.eventStartTime ||
        !this.eventEndTime
      ) {
        alert("One of your fields is missing");
        return;
      }
      if (
        this.eventStart.indexOf("/") !== -1 ||
        this.eventEnd.indexOf("/") !== -1 ||
        this.eventStart.indexOf("-") === -1 ||
        this.eventEnd.indexOf("-") === -1
      ) {
        alert("Please use the proper date format");
        return;
      }
      if (Date.parse(this.eventStart) > Date.parse(this.eventEnd)) {
        alert("Your start date is after your end date");
        return;
      }
      db.collection("calevents")
        .add({
          employee: this.eventEmployee,
          name: this.completeTitle,
          start: this.completeStart,
          end: this.completeEnd,
          details: this.eventDetails
        })
        .then(() => {
          alert("New Event has been added");
          this.dialog = false;
          this.$refs.form.reset();
        })
        .then(() => this.$router.go())
        .catch(error => console.log(error));
    },
    getStartDate(date) {
      this.eventStart = date;
    },
    getEndDate(date) {
      this.eventEnd = date;
    },
    getStartTime(time) {
      this.eventStartTime = time;
      console.log("complete start", this.completeStart);
    },
    getEndTime(time) {
      this.eventEndTime = time;
      console.log("complete end", this.completeEnd);
    }
  }
};
</script>

