<template lang="pug">
  v-row(class="fill-height")
    v-col
      v-sheet(class="mb-3")
        <!--touch this height-->
        v-toolbar(flat color='white')
          v-btn(class="mr-4" outlined color='grey darken-2' @click='setToday') Today  
          v-btn(fab text small color='grey darken-2' @click='prev')
            v-icon(small) mdi-chevron-left
          v-btn(fab text small color='grey darken-2' @click='next')
            v-icon(small) mdi-chevron-right
          span.d-none.d-sm-flex
            AddCalEvent(:employees='names').
          v-toolbar-title {{ title }}
          v-menu
            template(v-slot:activator='{ on }')
              v-btn(outlined color="grey darken-2" v-on="on")
                span {{ typeToLabel[type] }}
                v-icon(right) mdi-menu-down
            v-list
              v-list-item(@click='type = \'day\'')
                v-list-item-title Day
              v-list-item(@click='type = \'week\'')
                v-list-item-title Week
              v-list-item(@click='type = \'month\'')
                v-list-item-title Month
              v-list-item(@click='type = \'4day\'')
                v-list-item-title 4 days
        v-col(class="d-flex d-sm-none")
          AddCalEvent(:employees="names")     
      v-sheet(height="600")
        v-calendar(ref='calendar' v-model='focus' color='primary' :events='events' :event-color='getEventColor' :now='today' :type='type' @click:event='showEvent' @click:more='viewDay' @click:date='viewDay')
        v-menu(v-model='selectedOpen' :close-on-content-click='false' :activator='selectedElement' max-width='500px' offset-x)
          v-card.black---text(color='grey lighten-4' min-width='350px' max-width='500px' flat)
            v-toolbar(:color="selectedEvent.color" dark)
              v-btn(icon @click="editEvent(selectedEvent)")
                v-icon mdi-pencil
              v-toolbar-title(v-if="currentlyEditing !== selectedEvent.id") {{ selectedEvent.name }}
              v-toolbar-title(v-else)
                v-form
                  v-text-field(type='text' :placeholder='selectedEvent.name' v-model='selectedEvent.name')
              v-spacer
              v-btn(icon @click="delEvent(selectedEvent)")
                v-icon mdi-delete
            v-card-text.font-weight-bold(v-if='currentlyEditing !== selectedEvent.id')
              p Event Start: {{ selectedEvent.start }}
              p Event End: {{ selectedEvent.end }}
              p(v-if="selectedEvent.details") Event Details: {{ selectedEvent.details }}
            v-card-text(v-else)
              v-text-field(:placeholder='selectedEvent.start' v-model='selectedEvent.start' color='black')
              v-text-field(:placeholder='selectedEvent.end' v-model='selectedEvent.end' color='black')
              v-textarea(label='Details' v-model='selectedEvent.details' rows='1' color='black')
            v-card-actions
              v-btn(text color="secondary" @click="cancel") Cancel
              v-btn(text right color='secondary' @click='saveEvent(selectedEvent)') Save
</template>

<script>
import db from "@/components/firebaseInit";
import AddCalEvent from "@/components/AddCalEvent";

export default {
  name: "Calendar",
  components: {
    AddCalEvent
  },
  data: () => ({
    focus: "",
    today: new Date().toISOString().substr(0, 10),
    type: "week",
    typeToLabel: {
      month: "Month",
      week: "Week",
      day: "Day",
      "4day": "4 Days"
    },
   // employee: null,
    name: null,
    details: null,
    start: null,
    end: null,
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    currentlyEditing: null,
    events: [],
    colors: [
      "blue",
      "blue-grey",
      "indigo",
      "deep-purple",
      "cyan",
      "green",
      "orange",
      "deep-orange",
      "cyan",
      "teal",
      "grey darken-1",
      "red",
      "pink",
      "amber",
      "brown",
      "lime"
    ],
    names: []
  }),
  created() {
    db.collection("employees")
      .orderBy("employee_id")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          let first_name = doc.data().first_name;
          let last_name = doc.data().last_name;
          //let last_initial = doc.data().last_name.slice(0, 1);
          //let short_name = `${first_name} ${last_initial}`;
          let full_name = `${first_name} ${last_name}`;
          this.names.push(full_name);
        });
      });
  },
  computed: {
    title() {
      const { start, end } = this;
      if (!start || !end) {
        return "";
      }
      const startMonth = this.monthFormatter(start);
      const endMonth = this.monthFormatter(end);
      const suffixMonth = startMonth === endMonth ? "" : endMonth;
      const startYear = start.year;
      const endYear = end.year;
      const suffixYear = startYear === endYear ? "" : endYear;
      const startDay = start.day + this.nth(start.day);
      const endDay = end.day + this.nth(end.day);
      switch (this.type) {
        case "month":
          return `${startMonth} ${startYear}`;
        case "week":
        case "4day":
          return `${startMonth} ${startDay} ${startYear} - ${suffixMonth} ${endDay} ${suffixYear}`;
        case "day":
          return `${startMonth} ${startDay} ${startYear}`;
      }
      return "";
    },
    monthFormatter() {
      return this.$refs.calendar.getFormatter({
        timeZone: "UTC",
        month: "long"
      });
    }
  },
  methods: {
    cancel() {
      this.selectedOpen = false;
      this.currentlyEditing = null;
    },
    editEvent(ev) {
      this.currentlyEditing = ev.id;
    },
    delEvent(ev) {
      db.collection("calevents")
        .doc(ev.id)
        .delete();
      this.selectedOpen = false;
      this.currentlyEditing = null;
      const currentEvents = this.events.filter(event => event.id !== ev.id);
      this.events = currentEvents;
    },
    formatDate(a, withTime) {
      return withTime
        ? `${a.getFullYear()}-${a.getMonth() +
            1}-${a.getDate()} ${a.getHours()}:${a.getMinutes()}`
        : `${a.getFullYear()}-${a.getMonth() + 1}-${a.getDate()}`;
    },
    getEventColor(event) {
      return event.color;
    },
    getEvents() {
      db.collection("calevents")
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            let appData = doc.data();
            appData.id = doc.id;
            (appData.color = this.colors[this.rnd(0, this.colors.length - 1)]),
              this.events.push(appData);
          });
        });
    },
    next() {
      this.$refs.calendar.next();
    },
    nth(d) {
      return d > 3 && d < 21
        ? "th"
        : ["th", "st", "nd", "rd", "th", "th", "th", "th", "th", "th"][d % 10];
    },
    prev() {
      this.$refs.calendar.prev();
    },
    rnd(a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a;
    },
    saveEvent(ev) {
      if (!ev.name || !ev.start || !ev.end) {
        alert("One of your fields is missing");
        return;
      }
      if (
        ev.start.indexOf("/") !== -1 ||
        ev.end.indexOf("/") !== -1 ||
        ev.start.indexOf("-") === -1 ||
        ev.end.indexOf("-") === -1 ||
        ev.start.indexOf(":") === -1 ||
        ev.end.indexOf(":") === -1 ||
        ev.start.indexOf(" ") === -1 ||
        ev.end.indexOf(" ") === -1 ||
        ev.start.length !== 16 ||
        ev.end.length !== 16 ||
        isNaN(Date.parse(ev.start)) ||
        isNaN(Date.parse(ev.end))
      ) {
        alert("Please use the proper date format YYYY-MM-DD HH:MM");
        return;
      }
      if (Date.parse(ev.start) > Date.parse(ev.end)) {
        alert("Your start date is after your end date");
        return;
      }
      db.collection("calevents")
        .doc(this.currentlyEditing)
        .update({
          name: ev.name,
          start: ev.start,
          end: ev.end,
          details: ev.details
        })
        .catch(error => console.log(error));
      this.selectedOpen = false;
      this.currentlyEditing = null;
    },
    setToday() {
      this.focus = this.today;
    },
    viewDay({ date }) {
      this.focus = date;
      this.type = "day";
    },
    showEvent({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event;
        this.selectedElement = nativeEvent.target;
        setTimeout(() => (this.selectedOpen = true), 10);
      };
      if (this.selectedOpen) {
        this.selectedOpen = false;
        setTimeout(open, 10);
      } else {
        open();
      }
      nativeEvent.stopPropagation();
    }
  },
  mounted() {
    this.$refs.calendar.checkChange();
    this.getEvents();
  }
};
</script>
