<template lang="pug">
v-container
  v-row
    v-col
      p Statistics
      p Total Events: {{ events.length }}
      p Total Hrs: {{ totalHours }}
      p Events by Employee
      p(v-for="(key,value) in test") {{ value }}: {{ key }}
      p Week with the Most Events
      p Week with the Highest Cost
</template>

<script>
import db from "@/components/firebaseInit";

export default {
  name: "Stats",
  data() {
    return {
      events: [],
      employees: [],
      hours: [],
      totalHours: 0,
      employeeName: [],
      test: null
    };
  },

  mounted() {
    db.collection("calevents")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.events.push(doc.data());
        });
        for (const event of this.events) {
          this.employeeName.push(Object.values(event)[1]);

          let start = Date.parse(event.start.replace(/\s/g, "T"));
          let end = Date.parse(event.end.replace(/\s/g, "T"));
          let seconds = (end - start) / 1000;
          let hours = seconds / 3600;
          this.hours.push(hours);
        }
        this.totalHours = this.hours.reduce((total, hour) => total + hour);
        this.test = this.employeeName.reduce((prev, cur) => {
          prev[cur] = (prev[cur] || 0) + 1;
          return prev;
        }, {});
      })
    
    db.collection("employees")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.employees.push(doc.data());
        });
      })
  }
};
</script>

